import { Form, Typography, Col, Row, Button, Input, message } from "antd";
import React from "react";
import { signupFn } from "../services/auth";

export default function Signup() {
  const [form] = Form.useForm();

  async function handleSubmit(userInfo) {
    try {
      await signupFn(userInfo);
      message.success("Account Created");
    } catch (error) {
      message.error(error.response.data.message);
    }
  }
  return (
    <Row gutter={[16, 16]}>
      <Col xs={24} sm={24} md={{ offset: 8, span: 8 }}>
        <Typography.Title level={1}>Sign up</Typography.Title>
        <Form form={form} onFinish={handleSubmit} layout="vertical">
          <Form.Item name="username" label="Username:">
            <Input type="text" />
          </Form.Item>
          <Form.Item name="email" label="Email:">
            <Input type="email" />
          </Form.Item>
          <Form.Item name="password" label="Password:">
            <Input.Password />
          </Form.Item>
          <Button type="primary" htmlType="submit" block size="large">
            Sign up
          </Button>
        </Form>
      </Col>
    </Row>
  );
}
