import { useState, useEffect } from "react";
import {
  Col,
  Row,
  Card,
  Button,
  Typography,
  Skeleton,
  Avatar,
  Timeline,
} from "antd";
import { getAllResourcesPy } from "../services/projects";
import { Link } from "react-router-dom";
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import "./styles/Projects.css";
import NavbarCM from "../components/Navbar/NavbarCM";

const { Title } = Typography;
const { Meta } = Card;

function Projects() {
  const [resources, setResourcespy] = useState(null);

  useEffect(() => {
    async function getResources() {
      const {
        data: { projects },
      } = await getAllResourcesPy();
      setResourcespy(projects);
    }
    getResources();
  }, []);
  return (
    <div className="main-section">
      <div className="site-card-wrapper">
        <NavbarCM />
        <Row gutter={[16, 16]}>
          <div className="p-text-div">
            <Title
              level={2}
              style={{ paddingTop: "10px", alignSelf: "center" }}
            >
              Current Projects.
            </Title>
            <p className="p-text">
              This section allows you to have real-time information of the
              company's strategic projects portfolio reported by the projects
              teams during all execution phases.
            </p>
          </div>

          {resources ? (
            resources.map((resource) => (
              <Col xs={{ span: 24 }} md={{ span: 8 }} key={resource._id}>
                <Card
                  style={{ width: 350, height: 300 }}
                  extra={<Link to={`/resource/${resource._id}`}>Details</Link>}
                  cover={
                    <img
                      alt="example"
                      src="https://res.cloudinary.com/cristhianov/image/upload/v1615593000/PPTracker%20Final/Logo_nu2fbv.webp"
                    />
                  }
                  actions={[
                    <SettingOutlined key="setting" />,
                    <EditOutlined key="edit" />,
                    <EllipsisOutlined key="ellipsis" />,
                  ]}
                >
                  <Meta
                    avatar={
                      <Avatar src="https://res.cloudinary.com/cristhianov/image/upload/v1615591880/PPTracker%20Final/manager-ppm-blue_qx1eo0.png" />
                    }
                    title={resource.projectName}
                  />
                  <br />
                  <Timeline>
                    <Timeline.Item>
                      <div className="indicators-div">
                        <div className="round-indicator">III</div>
                        <p>Phase</p>
                      </div>
                    </Timeline.Item>
                    <Timeline.Item>
                      <div className="indicators-div">
                        <div className="round-indicator">{resource.CPI}</div>
                        <p>CPI</p>
                      </div>
                    </Timeline.Item>
                    <Timeline.Item>
                      <div className="indicators-div">
                        <div className="round-indicator">{resource.SPI}</div>
                        <p>SPI </p>
                      </div>
                    </Timeline.Item>
                    <Timeline.Item>
                      <Button>
                        <p>Budget Plan: $ {resource.anualBudget}</p>
                      </Button>
                    </Timeline.Item>
                    <Timeline.Item>
                      <Button>
                        <p>Budget Exe.: $ {resource.anualBudget}</p>
                      </Button>
                    </Timeline.Item>
                  </Timeline>
                </Card>
              </Col>
            ))
          ) : (
            <Skeleton active />
          )}
        </Row>
      </div>
    </div>
  );
}

export default Projects;
